@include keyframe(fadeIn) {
  0% {
      opacity: 0;
  }

  100% {
      opacity: 1;
  }
}

@include keyframe(scaleIn) {
  0% {
    transform: translateX(-50%) translateY(-50%) scale(0.7);
  }

  100% {
    transform: translateX(-50%) translateY(-50%) scale(1);
  }
}

body {
  &.modalOpen {
    overflow-y: hidden;

    .modal,
    .backdrop {
      display: flex;
      @include animation(20ms, 250ms, fadeIn);
    }

    .modal__content {
      @include upTo(sm) {
        @include animation(80ms, 150ms, fadeIn);
        @include animation(70ms, 250ms, scaleIn);
      }
    }
  }
}