.btn {
  font-weight: $BOLD;
  line-height: 1.3;
  cursor: pointer;
  @include fontSize(12px);
  letter-spacing: 0.04em;
  padding: 1.1em 1.3em;
  border-radius: 7px;
  box-shadow: 0 7px 14px 0 rgba($primary, 0.18);
  border: 0;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  transition: all $ANIMATION_TIMING $ANIMATION_EASING;
  white-space: nowrap;

  @include downTo(md) {
    width: 100%;
    text-align: center;
  }
  
  @include upTo(sm) {
    @include fontSize(14px);
  }

  &:hover {
    text-decoration: none;
    border: 1px solid $COLOR_FOOTER;
    background-color: $COLOR_FOOTER;
  }

  &--filled {
    text-decoration: none;

    &--primary {
      border: 1px solid $primary;
      background-color: $primary;
    }
    &--secondary {
      border: 1px solid $primary;
      background-color: $secondary;
    }
    &[disabled] {
      cursor: not-allowed;
      background-color: $COLOR_GREY;
      color: #ffffff;
      border-color: $COLOR_GREY;
    }
  }

  &--outlined {
    border: 1px solid #ffffff;
    background-color: transparent;
    text-decoration: none;
  }
}