.cFull {
  overflow: hidden;
  position: relative;
  display: flex;
  flex-direction: column;
}

.txc {
  text-align: center;
}
.txcDesktop {
  @include upTo(sm) {
   text-align: center;
  }
}

.overlay {
  position: absolute;
  display: none;
  top: 0;
  left: 0;
  z-index: 2;
  width: 100vw;
  height: 100vh;
  background-color: rgba(#000000, 0);
}

.section {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  padding: 1.5rem 0 2rem;
  
  @include upTo(xs) {
    padding: 2rem 0 4rem;

    &--noPadding {
      padding: 0;
    }
  }
}

.container {
  margin:auto;
  position: relative;
  width: 100%;
  max-width: 100%;
  padding-left: $PADDING_MOBILE;
  padding-right: $PADDING_MOBILE;
  display: flex;
  // flex-direction: column;

  @include upTo(xs) {
    max-width: $sm;
    padding-left: $PADDING_SM;
    padding-right: $PADDING_SM;
    flex-direction: row;
  }
  @include upTo(sm) {
    max-width: $md;
  }
  @include upTo(md) {
    max-width: $lg;
  }
  @include upTo(lg) {
    max-width: $xl;
  }

  &--small {
    padding: 0;
    @include upTo(md) {
      max-width: $SMALL_CONTAINER;
    }
  }
}

// IMGS
.backImgs {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  width: 100%;
  height: 100%;
  
  svg {
    position: absolute;

    &:nth-child(1) {
      top: -33rem;
      left: -55%;
      width: 180%;

      @include upTo(sm) {
        top: -64rem;
        left: -20%;
        width: 100%;
      }
    }

    &:nth-child(2) {
      top: -10rem;
      right: -30rem;
      width: 220%;

      @include upTo(sm) {
        top: -10rem;
        right: -32rem;
        width: 100%;
      }
    }
  }
}


.experiencia,
.comunidad,
.forms {
  @include upTo(sm) {
    padding-top: 8rem;
    padding-bottom: 8rem;
  }
}

// Experiencia - section
.experiencia {
  hgroup {
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-bottom: 3.9rem;

    h1 {
      position: relative;
      font-weight: $BLACK;
      line-height: 1.2;
      text-align: left;
      color: $COLOR_ORANGE;
      margin-bottom: 1.4rem;
      @include fontSize(50px);

      @include upTo(sm) {
        @include fontSize(64px);
      }

      &:nth-child(2) {
        width: 100%;
        position: absolute;
        top: -0.46em;
        left: 0;
        color: rgba(0,0,0,0);
        text-decoration: 17px rgba($COLOR_ORANGE, 0.2) solid underline;
        text-underline-position: under;
      }
    }
    p {
      @include fontSize(16px);
      font-weight: $REGULAR;
      line-height: 2.13;
      text-align: left;
      color: $COLOR_BLUE;
    }
  }

  .btn {
    @include break(xs) {
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }

  figure {
    order: -1;
    @include upTo(sm) {
      padding-left: 2em;
      order: 0;
    }
    svg {
      max-width: 100%;
    }
  }
}

// Comunidad - section
.comunidad {
  hgroup {
    display: flex;
    flex-direction: column;
    @include upTo(sm) {
      padding-left: 4em;
    }

    h2 {
      display: flex;
      flex-direction: column;
      position: relative;
      color: $COLOR_BLACK_TEXT_FORM;
      font-weight: $BLACK;
      @include fontSize(28px);
      margin-bottom: 2rem;

      small {
        color: $COLOR_ORANGE;
        @include fontSize(14px);
        letter-spacing: 0.69px;
        text-align: left;
        color: #f4a428;
        margin-bottom: 1.1em;
      }
      
    }
    p {
      @include fontSize(16px);
      font-weight: $REGULAR;
      line-height: 1.89;
      text-align: left;
      color: #f4a428;
      span {
        color: $COLOR_BLUE;
      }

      @include upTo(sm) {
        @include fontSize(18px);
      }
    }
  }

  figure {
    position: relative;
    @include upTo(sm) {
      top: -5rem;
      padding-left: 2em;
    }
    svg {
      max-width: 100%;
    }
  }
}

// formulario - section
.forms {
  background-color: $COLOR_GREY;
  padding-top: 4rem;
  padding-bottom: 8rem;

  .container {
    max-width: 460px;
  }

  header {
    width: 100%;
    margin-bottom: 4rem;

    h3 {
      color: $COLOR_BLACK_TEXT_FORM;
      @include fontSize(24px);
      font-weight: $BLACK;
      line-height: 1;
      text-align: left;
      margin-bottom: 1.4rem;

      @include upTo(sm) {
        @include fontSize(32px);
      }
    }
    p {
      @include fontSize(16px);
      font-weight: $REGULAR;
      line-height: 2.13;
      text-align: left;
      color: $COLOR_BLUE;
    }
  }

  form {
    display: flex;
    flex-direction: column;
    

    .form-row {
      width: 100%;
    }
    
  }
}