$ROOT : 12px;

$FONT_FAMILY : 'Libre Franklin', sans-serif;

// FONT-WEIGHT
$BLACK: 900;
$BOLD : 700;
$MEDIUM : 500;
$REGULAR : 400;
$LIGHT : 300;

$HEIGHT_MENU_MOBILE : 79px;

// GRID sizes
$grid-breakpoints : ( xs: 0, sm: 576px, md: 768px, lg: 992px, xl: 1170px );
$SMALL_CONTAINER : 950px;

// COLORS
$COLOR_BG_BODY : white;
$COLOR_BLUE : #5e6a8b;
$COLOR_LABELS : $COLOR_BLUE;
$COLOR_BLACK_TEXT_FORM : #363636;
$COLOR_GREY : #f4f5f8;
$COLOR_FOOTER : #212529;
$COLOR_ORANGE : #f4a428;
$COLOR_RED : #f42828;
$COLOR_GREEN : #28f454;

// SPACES
$PADDING_MOBILE: 1.667rem;
$PADDING_SM: 1.25rem;

// ANIMATIONS
$ANIMATION_EASING: cubic-bezier(0.22, 0.61, 0.36, 1);
$ANIMATION_TIMING: 250ms;
$ANIMATION_DELAY: 150ms;
$TRANSITION : all $ANIMATION_TIMING $ANIMATION_EASING;

// HEADER
$HEADER_MAX_HEIGHT : 70px;

// BOOTSTRAP CUSTOMS 
$primary: $COLOR_ORANGE !default;
$secondary: white !default;

// MODAL
$modal-inner-padding:               0 !default;
$modal-header-padding-y:            0 !default;
$modal-header-padding-x:            0 !default;
$modal-header-padding:              $modal-header-padding-y $modal-header-padding-x !default;