.modal {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 99;
  transition: $TRANSITION;

  .backdrop {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.4);
    z-index: 999;
    transition: $TRANSITION;
  }

  &__content {
    display: flex;
    align-items: center;
    flex-direction: column;
    position: fixed;
    width: 100%;
    background-color: #ffffff;
    padding: 40px 21px 45.5px;
    border: solid 1px #707070;
    z-index: 9999;
    transition: $TRANSITION;

    @include break(xs) {
      height: 100vh;
      justify-content: center;
    }

    @include upTo(sm) {
      max-width: 600px;
      top: 50%;
      left: 50%;
      transform: translateX(-50%) translateY(-50%);
    }
  }

  header {
    figure {
      display: flex;
      align-items: center;
      justify-content: center;
    }
    hgroup {
      margin: 2rem 0 2.35rem;
      h3 {
        color: $COLOR_BLACK_TEXT_FORM;
        @include fontSize(28px);
        font-weight: $BLACK;
        line-height: 1;
        text-align: center;
      }
    }
  }

  &__body {
    p {
      color: $COLOR_BLUE;
      @include fontSize(18px);
      font-weight: $REGULAR;
      line-height: 1.8;
      text-align: center;
    }
  }

  footer {
    margin-top: 3rem;
    display: flex;
  }
}